.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.parallax {
  /* The image used */
  background-image: url("https://wallpaperaccess.com/full/47178.jpg");

  /* Set a specific height   */
  height: 500px;

  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.parallax2 {
  /* The image used */
  background-image: url("https://images2.alphacoders.com/805/thumb-1920-805200.jpg");

  /* Set a specific height */
  height: 500px;

  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.carta{
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-color: #fcf3f3;
  background-clip: border-box;
  border: 1px solid rgba(0,0,0,.125);
  border-radius: .25rem;
}

.login{
  margin: 0;
  position: absolute;
  padding-top: 10%;

}


.inicio{
  margin: 40px;
  border: 2px solid #c40909;
  border-radius: 16px;
  font-size: 20px;
  text-align: center;
}

.contact{
  margin: auto;
  padding: 10px;
  width: 35%;
  padding-top: 5%;
}

.fondo1{
  background-image: url('https://blogs.esa.int/space19plus/files/2019/03/nebula.jpg');
  height: 800px;
}

.fondo2{
  background-image: url('https://images2.alphacoders.com/748/thumb-1920-748350.png');
  height: 800px;
}

.fondo3{
  background-image: url('https://www.10wallpaper.com/wallpaper/1920x1080/1208/Aesthetic_Dream_Space_HD_Desktop_wallpaper_16_1920x1080.jpg');
  height: 800px;
}

.contact-form{
  border-radius: 14px;
  padding: 20px;
  background-color: #fdeded;
}

.about{
  width:50%;
  height: 50%;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
